// vue
import Vue from 'vue'
// vuex
import Vuex from 'vuex'

// SDK
import QQMapWX from '../static/js/qqmap-wx-jssdk.js'

// api
import {getMovieDetail,getNowPlayingMovie,getLocalTheatres} from '../api/index.js' 

// Vuex
Vue.use(Vuex)

// store
export default new Vuex.Store({
	state: {
		// 
		city: '',
		/*  */
		nowPlayingList: [], 
		/*  */
		localTheatresList: [],
		/*  */
		userName: '',
		/*  */
		userAvatarUrl: '',
		/*  */
		provider: '',
		// id
		movieid: '',
		// 
		moviename: '',
		// appkey
		appkey: 'ed15316039b85bfd',
		// 
		moviedetail: {},
		// 
		orderList: []
	},
	mutations: {
		
	},
	getters: {
		
	},
	actions: {
		// 
		getCity(context) {
			// loading
			uni.showLoading({
				title: ''
			})
			// uni
			uni.authorize({
				scope: 'scope.userLocation', // 
				// 
				success() {
					// (api)
					let qqmapsdk = new QQMapWX({
						key: 'CPKBZ-7ALWD-47A42-HTF6K-HKXHZ-U4BCV'
					});
					// 
					uni.getLocation({
					    type: 'gcj02',
					    success: function (res) {
					        // console.log('' + res.longitude);
					        // console.log('' + res.latitude)
							
							// )
							qqmapsdk.reverseGeocoder({
								location: {
								  latitude: res.latitude,
								  longitude: res.longitude
								},
								success(res) {
									context.state.city =  res.result.address_component.city.slice(0,2)
									uni.hideLoading()
								}
							})
					    }
					});
					
				},
				// 
				fail(res) {
					console.log(res)
					// 
					uni.showToast({
						icon: 'none',
						title: '',
						duration: 2000
					})
				}
			})
		},
		
		
		// 
		getUserInfo(context) {
			// 
			uni.getProvider({
				service: 'oauth',
				success(res) {
					// console.log(res.provider)
					context.state.provider = res.provider
					//  
					uni.checkSession({
						success(res) {
							// console.log(res)
							return
						},
						fail(err) {
							console.log('' + err)
							// 
							uni.showToast({
								icon: 'none',
								title: '',
								duration: 2000
							})
						}
					})
					uni.login({
						provider: context.state.provider,
						success(res) {
							// console.log(res)
							// 
							uni.getUserInfo({
								provider: context.state.provider,
								success(res) {
									console.log(res)
									context.state.userName = res.userInfo.nickName
									context.state.userAvatarUrl = res.userInfo.avatarUrl
								},
								fail(err) {
									console.log('' + err)
								}
							})
						},
						fail(err) {
							console.log('' + err)
						}
					})
				},
				fail(err) {
					console.log('' + err)
				}
			})
		},
		
		// 
		getNowPlayMovie(context) {
			if(context.state.nowPlayingList.length > 0) {
				console.log(context.state.nowPlayingList) 
			} else {
				// cityidcitydateappkeycityidcitydateappkeykey
				getNowPlayingMovie({
					city: context.state.city,
					appkey: context.state.appkey
				}).then(res => {
					let [err, data] = res
					if (err) throw err
					context.state.nowPlayingList = data.data.result.list
					console.log(context.state.nowPlayingList)
				})
			}
		},
		
		// 
		getTheatres(context) {
			if(context.state.localTheatres.length > 0) {
				console.log(context.state.localTheatres) 
			} else {
				// cityidcitydateappkeycityidcitydateappkeykey
				getLocalTheatres({
					city: context.state.city,
					appkey: context.state.appkey
				}).then(res => {
					let [err, data] = res
					if (err) throw err
					context.state.localTheatresList = data.data.result.list
					// console.log(context.state.nowPlayingList)
				})
			}
		},
		
		// 
		getMovieDet(context) {
			console.log(context.state.movieid)
			console.log(context.state.appkey)
			// id
			getMovieDetail({
				movieid: context.state.movieid,
				appkey: context.state.appkey
			}).then(res => {
				let [err, data] = res
				if (err) throw err
				context.state.moviedetail = data.data.result
				console.log(data.data.result)
			})
		}
	}
})